.card {
  position: relative;
  scroll-snap-align: start;
}
.card img {
  border-radius: 10px;
}

.container {
  position: absolute;
  bottom: 0;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: #fff;
  margin: 16px;
}

.container h3 {
  font-size: 24px;
  font-weight: 400;
  display: inline;
  color: #fff;
  margin: 0;
}
